  watch: {
    '$route':'getPath'
  },
  computed: {},
  created() {
    this.init();
  },
  mounted() {
  },
  methods: {
    init() {
        this.wikiType = this.$route.params.wikiType;
        this.currentPage = storage.getItem('wikiCardCurrentPage')==undefined?1:parseInt(storage.getItem('wikiCardCurrentPage'));
        this.pageSize = storage.getItem('wikiCardPageSize')==undefined?12:parseInt(storage.getItem('wikiCardPageSize'));
        let params = {
            wikiType: this.wikiType,
            currentPage: this.currentPage,
            pageSize: this.pageSize,
        }
        this.getWikiCardsPage(params)
        if (storage.getItem('userInfo') != undefined) {
            this.getMyCollectList();
        }     
    },
    wikiCollection(isCollection, id) {
        let params = {
            wikiId: id,
        }
        if (isCollection) {
            apiWiki.wikiCollection(params).then((res)=>{
            if (res.success) {
                // this.wiki = res.result;
                this.$message.success(res.result);
                this.getMyCollectList()
            } else {
                this.$message.error(res.message);
            }
            })
        }else {
            apiWiki.wikiUnCollection(params).then((res)=>{
            if (res.success) {
                // this.wiki = res.result;
                this.$message.success(res.result);
                this.getMyCollectList()
            } else {
                this.$message.error(res.message);
            }
            })
        }
    },
    getMyCollectList() {
        apiWiki.getMyCollectList().then(res=>{
            if(res.success){
                this.$message.success('getMyCollectList');
                this.collectList = res.result;
            }else{
                this.$message.error(res.message);
            }
        })
    },
    handleClick(tab, event) {
        console.log(tab, event);
    },
    getPath(){
        console.log(this.$route.params.wikiType);
        this.wikiType = this.$route.params.wikiType;
        this.currentPage = 1;
        let params = {
            wikiType: this.wikiType,
            currentPage: this.currentPage,
            pageSize: this.pageSize,
        }
        this.getWikiCardsPage(params)

    },
    handleSizeChange(val) {
        storage.setItem('wikiCardCurrentPage', this.currentPage);
        storage.setItem('wikiCardPageSize', val);
        this.pageSize = val;
        let params = {
            wikiType: this.wikiType,
            currentPage: this.currentPage,
            pageSize: val,
        }
        this.getWikiCardsPage(params)
    },
    handleCurrentChange(val) {
        storage.setItem('wikiCardCurrentPage', val);
        storage.setItem('wikiCardPageSize', this.pageSize);
        this.currentPage == val;
        let params = {
            wikiType: this.wikiType,
            currentPage: val,
            pageSize: this.pageSize,
        }
        this.getWikiCardsPage(params)
    },
    getWikiCardsPage(params) {
        // 发送请求获取数据

        apiWiki.getWikiCards(params).then(res=>{
            if(res.success){
                this.$message.success('百科Card获取成功');
                this.cardsData = res.result.records;
                this.total = res.result.total;

            }else{
                this.$message.error(res.message);
            }
        })
    },
    toAnimlDetail(id) {
        // 路由跳转到动物详细页面
        this.$router.push({path: '/petswiki/wikiInfo/' + id});

    },
  },

};
